body {
  margin: 0;
  padding: 0;
  font-family: 'Josefin Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.profile-pic {
    width: 300px;
    padding: 20px;
}

.about-wrapper {
    display: flex;
    align-items: center;
    height: 500px;
}

.bio-wrapper {
    padding: 20px;
    margin-right: 20px;
    font-size: 18px;
}

.view-work-btn {
    background: none;
    padding: 15px 20px;
    -webkit-transition: 0.3s ease-in-out;
    transition: 0.3s ease-in-out;
    box-shadow: 10px 10px 20px lightgrey;
    border-radius: 0px;
}

.view-work-btn:hover {
    background: -webkit-linear-gradient(315deg, #606c88, #3f4c6b);
    background: linear-gradient(135deg, #606c88, #3f4c6b);
    color: #fff;
    -webkit-transform: scale(1.03, 1.03);
            transform: scale(1.03, 1.03);
    box-shadow: 10px 20px 40px grey;
    border: none;
}

div.text-center.jumbotron {
    background: white;
    border: 1px solid lightgrey;
    border-radius: 0px;
    height: 300px;
    padding-top: 40px;
    margin-bottom: 80px;
}

div.text-center.jumbotron h2 {
    margin: 10px;
    font-size: 30px;
}

@media (max-width: 768px) {
    .about-wrapper {
        display: block;
    }
}
.project-card {
    border-radius: 0;
    -webkit-transition: 0.3s ease-in-out;
    transition: 0.3s ease-in-out;
    box-shadow: 10px 10px 20px lightgrey;

}

.work-wrapper {
    height: 500px;
}

.project-card:hover {
    cursor: pointer;
    -webkit-transform: scale(1.03, 1.03);
            transform: scale(1.03, 1.03);
    box-shadow: 10px 20px 40px grey;
}

a.project-card.thumbnail {
    text-decoration: none;
}

div.caption p {
    font-family: 'Cormorant Garamond', serif;
    font-size: 16px;
}

.project-card {
    padding: 20px;
}
.resume-wrapper {
    border: 1px solid grey;
    padding: 20px;
    text-align: center;
    list-style-type: none;
    width: 960px;
}

@media (max-width: 1000px) {
    .resume-wrapper {
        max-width: 100%;
    }
}


form {
    text-align: center;
    padding: 20px;
}

.contact-input,
.contact-textarea {
    width: 50%;
    padding: 10px;
    margin: 10px;
}

.send-btn {
    background: none;
    padding: 15px 20px;
    -webkit-transition: 0.3s ease-in-out;
    transition: 0.3s ease-in-out;
    box-shadow: 10px 10px 20px lightgrey;
    border-radius: 0px;
    border: 1px solid lightgrey;
    margin: 20px;
}

.send-btn:hover {
    background: -webkit-linear-gradient(315deg, #606c88, #3f4c6b);
    background: linear-gradient(135deg, #606c88, #3f4c6b);
    color: #fff;
    -webkit-transform: scale(1.03, 1.03);
            transform: scale(1.03, 1.03);
    box-shadow: 10px 20px 40px grey;
    border: none;
}

.contact-intro {
    text-align: center;
    font-size: 16px;
}

.contact-wrapper {
    height: 500px;
}
.myNav {
    width: 1170px;
    display: flex;
    margin: 0 auto 60px;
    align-items: center;
    min-height: 100px;
    background: white;
    box-shadow: none;
    border: none;
    border-radius: 0;
    border-bottom: 1px solid #777777;
    /* border-width: 960px; */;

}

a.navbar-brand {
    font-size: 30px;
}

.tab {
    font-size: 16px;
}

@media (max-width: 1200px) {
    .myNav {
        max-width: 100%;
    }
}

@media (max-width: 768px) {
    .myNav {
        /* display: block; */
        max-width: 100%;
    }
}
